@import url(https://fonts.googleapis.com/css2?family=Josefin+Sans:wght@500&family=Josefin+Slab&display=swap);
*{margin:0;padding:0;box-sizing:border-box;outline:none;list-style-type:none}button{background:none;background:#fbcbc9;border:none;padding:0.5rem;border-radius:2px;transition:0.2s;cursor:pointer;border:1px solid #333;font-family:"Josefin Sans", sans-serif}button:hover{-webkit-filter:brightness(120%);filter:brightness(120%)}body{font-family:"Josefin Sans", sans-serif;max-width:1050px;margin:0 auto;background:#333;color:#f1f1f1}body .login-page{margin:0 auto;text-align:center}body .login-page h1{margin:40px 0}body main nav{display:flex;justify-content:space-between;align-items:center;min-height:50px;padding:1rem;background:#fbcbc9;box-shadow:2px 0 4px rgba(51,51,51,0.479);color:#222}body main nav div{cursor:pointer;padding:0.8rem 1rem;background:#eee;transition:0.2s;border:1px solid #be1558}body main nav div:hover{-webkit-filter:brightness(120%);filter:brightness(120%)}body main .create{margin:1rem 0;padding:1rem;background:#eee;width:600px;font-size:1rem}body main .create h4{font-weight:200}body main .create .form{display:flex;align-items:center;justify-content:space-between;margin-top:10px}body main .create .form input{padding:0.8rem;width:170px}body main .create .form textarea{min-width:320px;line-height:20px}body main .create .form textarea,body main .create .form input{resize:none;font-family:"Josefin Slab", serif;border:1px solid #be1558;padding:0.8rem;height:50px;font-size:1.2rem}body main .create .form textarea::-webkit-input-placeholder, body main .create .form input::-webkit-input-placeholder{font-family:"Josefin Slab", serif;line-height:25px}body main .create .form textarea:-ms-input-placeholder, body main .create .form input:-ms-input-placeholder{font-family:"Josefin Slab", serif;line-height:25px}body main .create .form textarea::-ms-input-placeholder, body main .create .form input::-ms-input-placeholder{font-family:"Josefin Slab", serif;line-height:25px}body main .create .form textarea::placeholder,body main .create .form input::placeholder{font-family:"Josefin Slab", serif;line-height:25px}body main .read ul{display:flex;flex-wrap:wrap;width:100%;align-items:center}body main .read ul li{background:#be1558;min-width:280px;max-width:330px;padding:2rem 2rem 1rem;margin:10px 10px;position:relative;text-align:center;height:100%;display:grid;align-items:center}body main .read ul li p{font-size:1.3rem;margin-bottom:1.5rem;font-family:"Josefin Slab", serif;font-weight:500}body main .read ul li h6{font-size:0.8rem;font-style:italic;font-weight:100}body main .read ul li .buttons-container{margin-top:1rem;text-align:right}body main .read ul li .buttons-container button{font-size:0.7rem;margin-left:10px;padding:5px}body main .read .item-container-update textarea{resize:none;height:90px;width:100%}body main .read .item-container-update input{margin-bottom:10px}

